import React, { Component } from "react";
import LinkedinSDK from "react-linkedin-sdk";

const responseLinkedin = response => {
  console.log(response);
};

class Contacts extends Component {
  state = {};
  render() {
    return (
      <div className="container">
        <div
          class="LI-profile-badge"
          data-version="v1"
          data-size="medium"
          data-locale="en_US"
          data-type="vertical"
          data-theme="light"
          data-vanity="marcinroszczyk"
        >
          <a
            class="LI-simple-link"
            href="https://es.linkedin.com/in/marcinroszczyk?trk=profile-badge"
          >
            Marcin Roszczyk
          </a>
        </div>
        <div className="container mt-4 text-left">
          <a
            class="github-button"
            href="https://github.com/mikolunar"
            aria-label="Follow @mikolunar on GitHub"
          >
            Follow @mikolunar
          </a>
        </div>
        <div className="container mt-4 align-content-center" />
        <img
          className="image"
          width="70px"
          high="70px"
          src={require("../media/python.png")}
        />
      </div>
    );
  }
}

export default Contacts;
